.page {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move;
  position: absolute;
  top: 20vmin;
  left: 38.5vmin;
}

.stopwatch__minutes-hand--speed-up {
  animation: 600s steps(60, end) infinite move;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  top: 2vmin;
  left: 39.25vmin;
}

.stopwatch__seconds-hand--speed-up {
  animation: 10s linear infinite move;
}

@keyframes move {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}
